<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">components</span>: {
    Datetime,
    Group,
    XButton
  },
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">format</span>: <span class="hljs-string">'YYYY-MM-DD HH:mm'</span>,
      <span class="hljs-attr">value1</span>: <span class="hljs-string">'2015-11-12'</span>,
      <span class="hljs-attr">value2</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">value3</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">value4</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">value5</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">value6</span>: <span class="hljs-string">'2016-08-18'</span>,
      <span class="hljs-attr">value7</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">value8</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">limitHourValue</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">startDate</span>: <span class="hljs-string">'2015-11-11'</span>,
      <span class="hljs-attr">endDate</span>: <span class="hljs-string">'2017-10-11'</span>,
      <span class="hljs-attr">formatValue</span>: <span class="hljs-string">'2017-10-11'</span>,
      formatValueFunction (val) {
        <span class="hljs-keyword">return</span> val.replace(<span class="hljs-regexp">/-/g</span>, <span class="hljs-string">'$'</span>)
      }
    }
  },
  <span class="hljs-attr">methods</span>: {
    toggleFormat () {
      <span class="hljs-keyword">this</span>.format = <span class="hljs-keyword">this</span>.format === <span class="hljs-string">'YYYY-MM-DD HH:mm'</span> ? <span class="hljs-string">'YYYY-MM-DD'</span> : <span class="hljs-string">'YYYY-MM-DD HH:mm'</span>
    },
    reRender () {
      <span class="hljs-keyword">this</span>.startDate = <span class="hljs-string">'2016-11-11'</span>
      <span class="hljs-keyword">this</span>.endDate = <span class="hljs-string">'2018-10-11'</span>
    },
    change (value) {
      <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'change'</span>, value)
    },
    clearValue (value) {
      <span class="hljs-keyword">this</span>.$data.value6 = <span class="hljs-string">''</span>
    },
    clearValue8 (value) {
      <span class="hljs-keyword">this</span>.$data.value8 = <span class="hljs-string">''</span>
    },
    setToday (value) {
      <span class="hljs-keyword">let</span> now = <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>()
      <span class="hljs-keyword">let</span> cmonth = now.getMonth() + <span class="hljs-number">1</span>
      <span class="hljs-keyword">let</span> day = now.getDate()
      <span class="hljs-keyword">if</span> (cmonth &lt; <span class="hljs-number">10</span>) cmonth = <span class="hljs-string">'0'</span> + cmonth
      <span class="hljs-keyword">if</span> (day &lt; <span class="hljs-number">10</span>) day = <span class="hljs-string">'0'</span> + day
      <span class="hljs-keyword">this</span>.$data.value7 = now.getFullYear() + <span class="hljs-string">'-'</span> + cmonth + <span class="hljs-string">'-'</span> + day
      <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'set today ok'</span>)
    }
  }
}
